@use "../../vars";
@use "sass:color";
.add{
    width: 100%;
    height: 100%;
    overflow: auto;
    h2{
        font-size: 1.1rem;
        letter-spacing: 5px;
        margin-bottom: 1rem;
    }
    .server-error{
        text-align: center;
        width: 100%;
        height: 20px;
    }
    .editForm {
        padding-top: 2.5rem;
        padding-left: 1rem;
      @media (max-width: 600px) {
        
        width: 100%;
      }
        width: 97%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-bottom: 1rem;
        .field-add{
            width: 100%;
            height: 2rem;
            gap: 1%;
            display: flex;
            align-items: flex-end;
            margin-bottom: .2rem;
            &.field-address{
                position: relative;
                .suggestionBox{
                    position: absolute;
                    bottom: 0;
                    left: 5%;
                    transform: translateY(100%);
                    background-color: rgb(250, 250, 250);
                    width: 80%;
                    overflow: auto;
                    max-height: 100px;
                    border-radius: 10px;
                  
                    cursor: pointer;
                    > div {
                        padding: .1rem;
                        font-size: .8rem;
                        &:hover{
                            background-color: vars.$primary-color;
                            color: white;
                        }
                    }
                }
            }
            .smIcon{
         
                font-size: 1.3rem;
                fill: vars.$tertiary-color;
              }
            .add-input{
            &::placeholder{
                color: vars.$primary-color;
            }  
            height: 60%;
            outline: none;
            width: 80%;
            color: vars.$primary-color;
            border: 1px solid vars.$gray;
            box-shadow: vars.$box-shadow;
            border-radius: 1rem;
            padding: .7rem;
           
            }
            &.field-types{
                height: 50px;
                margin: .5rem 0;
                align-items: center;
                .types-container{
                    display: flex;
                    align-items: center;
                    padding-left: .8rem;
                    gap: 6%;
                    @media (max-width: 600px) {
                        padding-left: 0;
                        gap: 3%;
                      }
                    width: 80%;
                    height: 100%;

                    .icon{
                        font-size: 2.4rem;
                        border: none;
                        fill: white;
                        &.active{
                            border: 1px solid vars.$secondary-color;
                            fill: vars.$secondary-color;
                        }
                    }
                }
            }
         
        }

        .areaContainer{
            margin-top: .5rem;
            width: 100%;
            height: 200px;
           
            gap: 1%;
            display: flex;
            .smIcon{
              font-size: 1.3rem;
              fill: vars.$tertiary-color;
            }
            textarea {
              width: 80%;
              height: 100%;
              resize: none;
              font-size: .9rem;
              outline: none;
              padding: .4rem;
              border: 1px solid vars.$gray;
              box-shadow: vars.$box-shadow;
              border-radius: 1rem;
              color: vars.$primary-color;
              &::placeholder{
                color: vars.$primary-color;
              }
            
              
            }
          }
    .buttonContainer{
        width: 100%;
        display: flex;
        align-items: center;
        padding-top: .5rem;
       
        justify-content: center;
        .btn{
          @media (max-width: 600px){
            width: 40%;
          }
          color: white;
          border: none;
          background-color: vars.$tertiary-color;
          width: 30%;
          border-radius: 7px;
          box-shadow: -4px 0px 4px 2px rgba(204, 204, 204, 0.25);
        }
      } 
    }
}